@keyframes showBullet {
  from {
    transform: scale(0, 0);
  }
  to {
    transform: scale(1, 1);
  }
}
@keyframes showText {
  from {
    transform: translateX(3rem);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes showLink {
  from {
    opacity: 0;
    width: 6rem;
  }
  to {
    opacity: 1;
    width: 100%;
    display: block;
  }
}
@keyframes showLinkText {
  from {
    color: transparent;
  }
  to {
    color: #5f458c;
  }
}
@keyframes showRightBlueLeaf {
  from {
    opacity: 0;
    transform: translateY(-30rem) rotateZ(-20deg);
  }
  25% {
    transform: rotateZ(10deg);
  }
  50% {
    transform: rotateZ(-10deg);
  }
  75% {
    transform: rotateZ(5deg);
    opacity: 1;
  }
  to {
    opacity: 1;
    transform: translateY(0) rotateZ(0);
  }
}
@keyframes showLeftBlueLeaf {
  from {
    opacity: 0;
    transform: translateY(-30rem) rotate(20deg);
  }
  25% {
    transform: rotate(-10deg);
  }
  50% {
    transform: rotate(10deg);
  }
  75% {
    transform: rotate(-5deg);
    opacity: 1;
  }
  to {
    opacity: 1;
    transform: translateY(0) rotate(0);
  }
}
@keyframes showGreenLeaf {
  from {
    opacity: 0;
    transform: translateY(-30rem) rotate(-20deg);
  }
  to {
    opacity: 1;
    transform: translateY(0) rotate(0);
  }
}
@keyframes showFlamingo {
  from {
    opacity: 0;
    transform: translate(-10rem, 100%) scale(0);
  }
  to {
    opacity: 1;
    transform: translate(0, 0) scale(1);
  }
}
@keyframes rotateFlamingo {
  from {
    transform: translate(-0.3rem, -0.6rem) rotate(3deg);
  }
  50% {
    transform: translate(0.3rem, 0.6rem) rotate(-3deg);
  }
  to {
    transform: translate(-0.3rem, -0.6rem) rotate(3deg);
  }
}
@keyframes showSuitcase {
  from {
    opacity: 0;
    transform: translate(-10rem, 100%) scale(0);
  }
  to {
    opacity: 1;
    transform: translate(0, 0) scale(1);
  }
}
