  width: 300px;
  height: 300px;
  position: absolute;
  top: 50%;
  left: calc(50% - 250px);
  display: block;
  transform-origin: center center;
  animation-name: showFlamingo, rotateFlamingo;
  animation-duration: 0.5s, 2.5s;
  animation-timing-function: ease-out;
  animation-delay: 0.3s, 0.8s;
  animation-fill-mode: both, forwards;
  animation-iteration-count: 1, infinite;
  animation-play-state: paused;
}

#result2.screen--show img {
  animation-play-state: running;
}

@keyframes rollInFromRight {
  from {
    transform: translateX(5rem) rotate(90deg);
    opacity: 0;
  }
  to {
    transform: translateX(0) rotate(0);
    opacity: 1;
  }
}
@keyframes rollInFromleft {
  from {
    transform: translateX(-5rem) rotate(-90deg);
    opacity: 0;
  }
  to {
    transform: translateX(0) rotate(0);
    opacity: 1;
  }
}
.active .slider__control--prev {
  animation: rollInFromleft 0.5s 1s both;
}
.active .slider__control--next {
  animation: rollInFromRight 0.5s 1s both;
}

.result--negative .result__button .result__button--image {
  animation: rollInFromRight 0.5s 1s both;
  animation-play-state: paused;
}
.result--negative .result__button .result__button--text {
  animation: fadeIn 0.5s 0.5s both;
  animation-play-state: paused;
}

.screen--show .result--negative .result__button span {
  animation-play-state: running;
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.screen--prizes.active:not(.screen--next) .screen__footer-note {
  animation: fadeOut 0.5s both;
}

.screen--rules.screen--next:not(.active) .disclaimer {
  animation: fadeIn 0.5s both;
}

.screen--rules.active:not(.screen--next) .disclaimer ul {
  animation: fadeOut 0.5s both;
}

.screen--prizes.screen--next:not(.active) .screen__footer-note {
  animation: fadeIn 0.5s both;
}

@media (max-width: 1024px) and (orientation: portrait) {
  .screen--prizes .screen__footer {
    transition-property: width, height, color;
    transition-duration: 0.5s;
    transition-delay: 0.5s, 0.5s, 0s;
    transition-timing-function: ease;
    color: transparent;
  }
  .screen--prizes .screen__footer--full {
    transition-delay: 0s, 0s, 0.5s;
    color: #5f458c;
  }
}
@keyframes fadeInJourneys {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes fadeInCodes {
  0% {
    opacity: 0;
    transform: translateX(2rem);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes jumpInLetter {
